  font-size: 1.25rem;
  letter-spacing: 0.125rem;
  line-height: 1;
  border: 1px solid #fff;
  background: #333;
  color: #fff;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  transition: ease 0.3s;
  z-index: 1;
}
@media screen and (min-width: 768px) {
