}

.btn::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: translateX(-100%);
  transition: 0.3s ease-in-out;
  z-index: -1;
}

@media screen and (min-width: 768px) {
  .btn:hover::before {
    transform: translateX(0);
    opacity: 1;
