@use "global" as *;

.header{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: rem(50);
  background: rgba($black, 0.5);
  border: 1px solid #000;
  z-index: 10;
  @include mq(md){
    border: initial;
  }
}
.header.addColor{
  background: $black;
}

.header__inner {
  width: 100%;
  height: inherit;
  margin: 0 auto;
  padding: 0 rem(12) 0 $padding-sp;
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include mq(md){
    padding: 0 0 0 rem(32);
  }
}
.header__title{
  position: relative;
  width: 100%;
  max-width: rem(73);
  margin-top: rem(2);
  z-index: 20;
  @include mq(md){
    max-width: rem(96);
    margin-top: rem(5);
  }
}
.header__nav{
  display: none;
  @include mq(md){
    display: block;
    height: inherit;
  }
}
.header__lists{
  display: flex;
  align-items: center;
  height: inherit;
}
.header__list + .header__list{
  margin-left: rem(30);
}
.header__link{
  position: relative;
  color: $white;
  letter-spacing: 2px;
}
.header__link::after{
  content: "";
  position: absolute;
  bottom: rem(-4);
  left: 0;
  width: 100%;
  height: rem(1);
  background: #FFEA2E;
  transition: all .3s;
  transform: scale(0, 1);
  transform-origin: center top;
}
.header__link:hover::after{
  @include mq(md) {
    opacity: 1;
    transform: scale(1, 1);
  }
}
.header__list--white{
  background: $white;
  height: inherit;
}
.header__list--white .header__link{
  color: $black;
  height: inherit;
  display: flex;
  align-items: center;
  padding: 0 rem(31);
}
.header__list--white:hover {
  @include mq(md) {
    background: $text-color;
    .header__link{
      color: $white;
    }
    .header__link::after{
      display: none;
    }
  }
  
}
.header__btn{
  position: relative;
  width: rem(26);
  height: rem(15);
  margin-right: rem(8);
  margin-bottom: rem(11);
  z-index: 20;
  @include mq(md){
    display: none;
  }
}
.header__btn span{
  display: block;
  width: 100%;
  height: rem(2);
  border-radius: rem(5);
  background-color: $white;
  position: absolute;
  transition: transform .5s, opacity .5s;
}
.header__btn span:nth-child(1) {
  top: 0;
}
.header__btn span:nth-child(2){
  top: 0;
  bottom: 0;
  margin: auto;
  }
.header__btn span:nth-child(3) {
  bottom: 0;
  }

//タップ後のハンバーガー
.header__btn.is-active {
  width: rem(23);
  margin: 0 0 rem(1) 0;
}
.header__btn.is-active span:nth-child(1) { 
  transform: translateY(rem(7)) rotate(-45deg);
  }
.header__btn.is-active span:nth-child(2) {
  opacity: 0;
  }
.header__btn.is-active span:nth-child(3) {
  transform: translateY(rem(-6)) rotate(45deg);
  }

//スライドメニュー
.header__spnav {
  display: block;
  position: fixed;
  z-index: 10;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  transform: translateX(100vw);
  transition: all .3s;
  background-color: $black;
  @include mq(md) {
    display: none;
  }
}
.header__splists {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin-top: rem(208);
  overflow: scroll;
}
.header__splist {
  list-style: none;
  text-align: center;
  padding-left: rem(4);
}
.header__splist + .header__splist {
  padding-top: rem(31);
}
.header__splink {
  font-size: rem(16);
  text-decoration: none;
  letter-spacing: 2px;
  color: $white;
}
//activeクラスをつけたら、スライドイン
.header__spnav.is-active {
  transform: translateX(0);
}